import React from 'react';
import SearchIcon from './search.svg';
import './App.css';
import { useEffect,useState } from 'react';
import MovieCard from './MovieCard';
import ModalVideo from 'react-modal-video';

const API_URL = 'http://www.omdbapi.com?apikey=eec3c245'

function App() {

  const [searchTerm, setSearchTerm] = useState("batman");
  const [movies, setMovies] = useState([]);
  const [isOpen, setOpen] = useState(false);

  const searchMovies = async (title) => {
    const response = await fetch(`${API_URL}&s=${title}`);
    const data = await response.json();
    // console.log(data)
    setMovies(data.Search);
  };
  let genre = [];
  let genreFiltered = [];

  const searchMoviesByID = async (imdbID, Genre) => {
    const response = await fetch(`${API_URL}&i=${imdbID}`);
    const data2 = await response.json();
    // console.log(data2.Genre);
    genre.push({'id':`${imdbID}`,'value':`${data2.Genre}`})
      genreFiltered = genre.filter(element => {
        return element.value.indexOf(Genre) !== -1;
      });
      console.log(genreFiltered);
      for (let i = 0; i < genreFiltered.length; i++) {
        const response1 = await fetch(`${API_URL}&i=${genreFiltered[i].id}`);
        const data3 = await response1.json();
        console.log(data3)
        setMovies(data3)
      }
      // const response1 = await fetch(`${API_URL}&i=${genreFiltered.id}`);
      // const data3 = await response1.json();
      // setMovies(data3.Search)
  };

  const searchMoviesByType = async (Type) => {
    if(Type !== 'all'){
      const response = await fetch(`${API_URL}&s=${searchTerm}&type=${Type}`);
      const data1 = await response.json();
      // console.log(data1.Search)
      // searchMoviesByID(data1.Search.imdbID)
      setMovies(data1.Search);
    }
    else{
      const response = await fetch(`${API_URL}&s=${searchTerm}`);
      const data1 = await response.json();
      // console.log(data1.Search)
      // searchMoviesByID(data1.Search.imdbID)
      setMovies(data1.Search);
    }
  };


  const searchMoviesByYear = async (Year) => {
    console.log(Year)
    console.log(searchTerm)
    const response = await fetch(`${API_URL}&s=${searchTerm}&y=${Year}`);
    const data1 = await response.json();
    console.log(data1.Search)
    // searchMoviesByID(data1.Search.imdbID)
    setMovies(data1.Search);
  };

  const searchMoviesByGenre = async (Genre) => {
    const response = await fetch(`${API_URL}&s=${searchTerm}`);
    const data1 = await response.json();
    // setMovies(data1.Search);
    console.log(data1.Search)
    for (let i = 0; i < data1.Search.length; i++) {
        searchMoviesByID(data1.Search[i].imdbID, Genre)
    }
  };


  useEffect(() => {
    searchMovies('batman');
  },[]);

  const renderList = () => {
    let li = [];
    for (let i = 2000; i <= 2022; i++) {
      li.push(<li onClick={() => searchMoviesByYear(`${i}`)}>{i}</li>);
    }
    return li;
  };
  
  return (
    <div className="app">
      <h1>WonderLand</h1>
      <div className='search'>
          <input placeholder='Search for movies' value={searchTerm} onChange={(e) => setSearchTerm(e.target.value)}/>
          <img src={SearchIcon} alt='search' onClick={() => searchMovies(searchTerm)}/>
      </div>
      {/* <div className='white-font genre-list'>
        <h2>Genre</h2>
        <ul>
          <li onClick={() => searchMoviesByGenre('All')}>All</li>
          <li onClick={() => searchMoviesByGenre('Action')}>Action</li>
          <li onClick={() => searchMoviesByGenre('Crime')}>Crime</li>
        </ul>
      </div>
      <br></br> */}
      <div className='white-font genre-list'>
        <h2>Type</h2>
        <ul>
          <li onClick={() => searchMoviesByType('all')}>All</li>
          <li onClick={() => searchMoviesByType('movie')}>Movie</li>
          <li onClick={() => searchMoviesByType('series')}>Series</li>
        </ul>
      </div>
      <br></br>
      <div className='white-font genre-list'>
        <h2>Year</h2>
        <ul>
          {renderList()}
        </ul>
      </div>
      <div className=''>
        {movies?.length > 0 ? (
          <div className="container">
            {movies.map((movie) => (
              <MovieCard movie={movie} />
            ))}
          </div>
        ) : (
          <div className="empty">
            <h2>No movies found</h2>
          </div>
        )}
      </div>
      <>
          <ModalVideo channel='youtube' autoplay isOpen={isOpen} videoId="L61p2uyiMSo" onClose={() => setOpen(false)} />

          {/* <button className="btn-primary" onClick={()=> setOpen(true)}>VIEW DEMO</button> */}
      </>
    </div>
  );
}

export default App;
